import type { NumberFormatEnum } from './number';

const createZeroString = (length = 1): string => {
  return '0'.repeat(length);
};

export const formatWithSeparators = (
  num: number,
  thousandSeparator: string,
  decimalSeparator: string,
  decimalPlaces: number
): [string, string, string] => {
  const [integerPart, fractionalPart = ''] = num.toString().split('.');
  const integerFormatted = integerPart.replace(/\B(?=(\d{3})+(?!\d))/g, thousandSeparator);
  const hasDecimal = !!decimalPlaces;
  const fractionalFormatted = hasDecimal
    ? (fractionalPart + createZeroString(decimalPlaces)).slice(0, decimalPlaces)
    : '';

  return [integerFormatted, hasDecimal ? decimalSeparator : '', fractionalFormatted];
};

const SEPARATOR: Record<string, string> = {
  space: ' ',
  period: '.',
  comma: ',',
};

export const formatNumber = (
  input: string | number | undefined,
  type: NumberFormatEnum,
  decimalPlaces: number
): string => {
  if (input !== 0 && !input) {
    return '';
  }

  const num = typeof input === 'string' ? parseFloat(input) : input;

  if (isNaN(num)) {
    console.error('Invalid number input');

    return '';
  }

  const [thousandSeparatorName, decimalSeparatorName] = type.split('-');
  const thousandSeparator = SEPARATOR[thousandSeparatorName] || SEPARATOR.space;
  const decimalSeparator = SEPARATOR[decimalSeparatorName] || SEPARATOR.period;

  return formatWithSeparators(num, thousandSeparator, decimalSeparator, decimalPlaces).join('');
};

const removeTrailingDot = (input: string): string => (input?.endsWith('.') ? input.slice(0, -1) : input);

const replaceEmptyNegative = (input: string): string => (input === '-' ? '-1' : input);

const roundToDecimal = (num: string, decimalPlaces: number): number => {
  const factor = Math.pow(10, decimalPlaces);

  return Math.round(+num * factor) / factor;
};

export const processNumberForSave = (input: string, decimalPlaces: number): number =>
  roundToDecimal(removeTrailingDot(replaceEmptyNegative(input)), decimalPlaces);

export const isLengthTooBig = (input: string): boolean => {
  if (input.length <= 16) {
    return false;
  }

  const [num, decimal] = input.split('.');

  return num.length > 16 || decimal.length > 16;
};

export const placeCaretToEnd = (element: HTMLInputElement | HTMLTextAreaElement, scrollToEnd = false) => {
  const length = element.value.length;
  element.setSelectionRange(length, length);

  if (scrollToEnd) {
    element.scrollTop = element.scrollHeight;
  }
};

export const makeCaretFocusHandler = (scrollToEnd = false) => {
  return function handleFocus(event: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>) {
    placeCaretToEnd(event.target, scrollToEnd);
  };
};
