export enum ComponentMode {
  View = 'view',
  Edit = 'edit',
}

export interface PuiUser {
  id: string;
  name: string;
  avatarUrl?: string;
  email: string;
  details?: string;
  phone?: string;
}

export interface PuiContact {
  id: string;
  name: string;
  email: string;
  avatarUrl?: string;
  address?: {
    details: {
      city: string;
      country: string;
      postalCode: string;
      route: string;
    };
  };
  company?: string;
  position?: {
    id: string;
    jobTitle: string;
  };
  birthday?: string;
  phones?: {
    id: string;
    label: string;
    phone: string;
  }[];
  socialMedia?: string[];
  additionalEmails?: {
    id: string;
    label: string;
    email: string;
  }[];
  nameDay?: string;
}

