import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, InputProps, SliderProps, TypographyProps } from '@mui/material';
import { Slider } from '@mui/material';
import MuiInput from '@mui/material/Input';

import { PuiBox, PuiStyled, PuiTypography } from '../../components';

export interface StyledSliderProps {
  height?: string;
  background?: string;
  border?: string;
  texture?: string;
}

export const Wrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  height: '24px',
  display: 'flex',
  alignItems: 'center',
  gap: '8px',
});

export const Track: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  flex: 1,
  height: '4px',
  backgroundColor: '#F5F5F5',
  borderRadius: '2px',
  position: 'relative',
});

export const ViewProgress: StyledComponent<BoxProps & { color?: string; displayType: string }> = PuiStyled(PuiBox)<{
  color?: string;
  displayType: string;
}>(({ color, displayType, theme }) => ({
  position: 'absolute',
  height: '100%',
  left: 0,
  borderRadius: '2px',
  backgroundColor: color || theme.palette.success.main,
  border: displayType === 'big' ? `1px solid ${color || theme.palette.success.main}` : 'none',
}));

export const SliderProgress: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  position: 'absolute',
  height: '100%',
  left: 0,
  borderRadius: '2px',
});

export const ValueText: StyledComponent<TypographyProps> = PuiStyled(PuiTypography)({
  fontSize: '12px',
  minWidth: '20px',
});

export const StyledSlider: StyledComponent<SliderProps & StyledSliderProps> = PuiStyled(Slider)<StyledSliderProps>(
  ({ theme, height, background, border, texture }) => ({
    color: texture,
    height: height,
    '& .MuiSlider-track': {
      border: 'none',
    },
    '&.view-mode': {
      cursor: 'default',
      '.MuiSlider-thumb': { display: 'none' },
    },
    '& .MuiSlider-thumb': {
      height: '12px',
      width: '12px',
      backgroundColor: 'currentColor',
      border: `2px solid ${theme.palette.background.paper}`,
      boxShadow: `0px 0px 3px 0px ${theme.palette.grey[50]}`,
      '&:focus, &:hover, &.Mui-active, &.Mui-focusVisible': {
        boxShadow: 'inherit',
      },
      '&::before': {
        display: 'none',
      },
    },
    '& .MuiSlider-rail': {
      backgroundColor: background,
      opacity: 1,
      border: border,
    },
    '& .MuiSlider-valueLabel': {
      lineHeight: '16px',
      fontSize: 10,
      background: 'unset',
      padding: 4,
      minWidth: '21px',
      height: '17px',
      backgroundColor: texture,
      borderRadius: '4px',

      '&::before': {
        zIndex: -1,
      },
    },
  })
);

export const PercentageSign: StyledComponent<TypographyProps> = PuiStyled(PuiTypography)({
  margin: 0,
  fontSize: '12px',
});

export const InputWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
});

export const InputViewWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  cursor: 'default',
});

export const Input: StyledComponent<InputProps> = PuiStyled(MuiInput)({
  width: '20px',
  '& input': {
    padding: 0,
    fontSize: '12px',
  },
  ["input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button"]: {
    margin: 0,
    ['WebkitAppearance']: 'none',
  },
  ["input[type='number']"]: {
    ['MozAppearance']: 'textfield',
  },
});
