import type { StyledComponent } from '@emotion/styled';
import type { TypographyProps } from '@mui/material';

import { PuiStyled, PuiTypography } from '../../../';

export const TextField: StyledComponent<TypographyProps> = PuiStyled(PuiTypography)({
  overflow: 'hidden',
  padding: '0px 16px',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
  lineHeight: '1.5',
  '&:focus-visible': {
    outline: 'none',
  },
});
