import type { InputProps } from '@mui/material';
import type { MuiTelInputCountry, MuiTelInputInfo } from 'mui-tel-input';
import type { ReactNode } from 'react';
import { forwardRef } from 'react';

import { StyledPhoneInput } from './StyledComponents';

interface PhoneInputProps {
  value: string;
  defaultCountry: string;
  validationError: null | string;
  styleMod?: 'bordered' | 'startIcons' | 'static' | 'restValues';
  disabled?: boolean;
  placeholder?: string;
  isReadOnly?: boolean;
  sx?: InputProps['sx'];
  InputProps?: {
    startAdornment?: ReactNode;
    endAdornment?: ReactNode;
  };
  onChange: (value: string, countryCode: string) => void;
  autoFocus?: boolean;
  className?: string;
}

export const PhoneInput = forwardRef<HTMLDivElement, PhoneInputProps>((props, ref) => {
  const {
    value,
    validationError,
    InputProps,
    placeholder = '+00 (000) 000-0000',
    disabled,
    styleMod,
    defaultCountry,
    isReadOnly,
    sx,
    onChange,
    autoFocus = false,
    className,
  } = props;
  const handleChange = (value: string, info: MuiTelInputInfo) => onChange(value, info.countryCode || defaultCountry);

  const haveCustomPlaceholder = props.placeholder ? 'show-placeholder' : '';

  const baseClassName = className || '';
  const styleModClass = styleMod ? `mod-${styleMod}` : '';
  const placeholderClass = haveCustomPlaceholder;

  return (
    <StyledPhoneInput
      className={`${baseClassName} ${styleModClass} ${placeholderClass}`.trim()}
      maxRows={12}
      ref={ref}
      value={value}
      disabled={disabled}
      placeholder={placeholder}
      helperText={validationError}
      error={!!validationError}
      InputProps={InputProps}
      onChange={handleChange}
      isReadOnly={isReadOnly}
      disableDropdown={isReadOnly}
      sx={sx}
      MenuProps={{
        sx: {
          '& .MuiPaper-root': {
            maxHeight: '280px',
          },
          '& .MuiButtonBase-root .MuiListItemText-primary, & .MuiButtonBase-root .MuiTelInput-Typography-calling-code':
            {
              fontSize: '13px',
            },
          '& .MuiButtonBase-root .MuiTelInput-FlagImg': {
            width: '19px',
            height: '13px',
          },
        },
      }}
      inputProps={{
        readOnly: isReadOnly,
        onClick: isReadOnly && value ? () => window.open(`tel:${value}`) : undefined,
      }}
      defaultCountry={defaultCountry as MuiTelInputCountry}
      autoFocus={isReadOnly === undefined ? autoFocus : !isReadOnly}
    />
  );
});
