import type { TextFieldProps } from '@mui/material';
import type { ChangeEvent, FC, KeyboardEvent, ReactNode, RefObject } from 'react';
import { forwardRef } from 'react';

import { StyledTextField } from '../../StyledComponents';

interface EmailInputProps {
  value: string;
  validationError: null | string;
  styleMod?: 'bordered' | 'borderless';
  disabled?: boolean;
  placeholder?: string;
  InputProps?: {
    startAdornment?: ReactNode;
    endAdornment?: ReactNode;
  };
  sx?: TextFieldProps['sx'];
  onChange: (value: string) => void;
  onKeyDown: () => void;
  autoFocus?: boolean;
}
type RefType = ((instance: HTMLDivElement | null) => void) | RefObject<HTMLDivElement> | null | undefined;

export const EmailInput: FC<EmailInputProps> = forwardRef((props, ref: RefType) => {
  const {
    value,
    validationError,
    InputProps,
    placeholder = 'youremail@gmail.com',
    disabled,
    styleMod,
    sx,
    onKeyDown,
    onChange,
    autoFocus = false,
  } = props;
  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => onChange(e.currentTarget.value);
  const handleKeyDown = (e: KeyboardEvent<HTMLInputElement>) => !e.shiftKey && e.key === 'Enter' && onKeyDown();

  return (
    <StyledTextField
      type='email'
      size='small'
      className={styleMod ? `mod-${styleMod}` : undefined}
      maxRows={12}
      ref={ref}
      value={value}
      disabled={disabled}
      placeholder={placeholder}
      helperText={validationError}
      error={!!validationError}
      InputProps={InputProps}
      sx={sx}
      onChange={handleChange}
      onKeyDown={handleKeyDown}
      autoFocus={autoFocus}
    />
  );
});
