import type { BoxProps, InputProps } from '@mui/material';

import type { ComponentMode } from '../../types';

export enum NumberFormatEnum {
  SPACE_PERIOD = 'space-period',
  SPACE_COMMA = 'space-comma',
  COMMA_PERIOD = 'comma-period',
  PERIOD_COMMA = 'period-comma',
}

export interface PuiNumberEditProps {
  value: number | undefined;
  disabled?: boolean;
  percentage?: boolean;
  signed?: boolean;
  decimalPlaces?: number;
  wrapperSx?: BoxProps['sx'];
  inputSx?: InputProps['sx'];
  isLoading?: boolean;
  onSave: (number: number | undefined) => void;
  onChange?: (number: number) => void;
  autoFocus?: boolean;
  placeholder?: string;
}

export interface PuiNumberProps extends PuiNumberEditProps {
  mode?: ComponentMode;
  format?: NumberFormatEnum;
}
