import type { BoxProps, CheckboxProps } from '@mui/material';
import type { ChangeEvent } from 'react';

import type { ComponentMode } from '../../types';

export enum BooleanDisplayConfigType {
  Checkmark = 'checkmark',
  Heart = 'heart',
  Star = 'star',
  ThumbsUp = 'thumbs-up',
  Flag = 'flag',
  Switch = 'switch',
}

export interface PuiBooleanEditProps {
  checked: boolean;
  disabled?: boolean;
  color?: string;
  displayType: BooleanDisplayConfigType;
  sx?: CheckboxProps['sx'];
  iconSx?: BoxProps['sx'];
  onChange: (event: ChangeEvent<HTMLInputElement>, checked: boolean) => void;
}

export interface PuiBooleanProps extends PuiBooleanEditProps {
  mode?: ComponentMode;
}

export interface PuiCheckboxViewProps {
  checked: boolean;
  displayType: BooleanDisplayConfigType;
  color?: string;
  iconSx?: BoxProps['sx'];
  sx?: BoxProps['sx'];
}

export interface PuiBooleanDefaultViewProps {
  checked: boolean;
  color: string;
  displayType: BooleanDisplayConfigType;
  sx?: BoxProps['sx'];
}

export interface PuiSwitchViewProps {
  checked: boolean;
  color: string;
  sx?: BoxProps['sx'];
}
