import type { StyledComponent } from '@emotion/styled';
import type { BoxProps } from '@mui/material';

import { PuiBox, PuiStyled } from '../../../ui';

export const DotsContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  width: '100%',
  textAlign: 'center',
});

interface DotProps extends BoxProps {
  delay: string;
  color: string;
}

export const Dot: StyledComponent<DotProps> = PuiStyled(PuiBox)<DotProps>(({ delay, color }) => ({
  display: 'inline-block',
  margin: '0 0.4em 0 0',
  width: '20px',
  height: '20px',
  borderRadius: '50%',
  backgroundColor: color,
  animation: 'dots 2s infinite ease-in-out',
  animationDelay: delay,
  '@keyframes dots': {
    '50%': {
      opacity: 0,
      transform: 'scale(0.7) translateY(10px)',
    },
  },
}));
