export const deepMerge = <T extends object, S extends object>(target: T, source: S): T & S => {
  if (!target || !source || typeof target !== 'object' || typeof source !== 'object') {
    return source as T & S;
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const result: any = Array.isArray(target) ? [...target] : { ...target };

  Object.keys(source).forEach(key => {
    const sourceKey = key as keyof typeof source;
    const sourceValue = source[sourceKey];
    const targetValue = result[key];

    if (sourceValue && typeof sourceValue === 'object') {
      if (Array.isArray(sourceValue)) {
        result[key] = [...sourceValue];
      } else if (sourceValue instanceof Date) {
        result[key] = new Date(sourceValue.getTime());
      } else if (targetValue && typeof targetValue === 'object') {
        result[key] = deepMerge(targetValue, sourceValue);
      } else {
        result[key] = { ...sourceValue };
      }
    } else {
      result[key] = sourceValue;
    }
  });

  return result;
};
