import type { BoxProps } from '@mui/material';
import { forwardRef } from 'react';

import { PuiBox } from '../../../';
import { formatPhoneNumber, getFlagUrl } from '../utils';
import { FlagImage, PhoneText, StyledViewWrapper } from './StyledComponents';

interface PhoneViewInputProps {
  value: string;
  defaultCountry: string;
  sx?: BoxProps['sx'];
}

export const PhoneViewInput = forwardRef<HTMLDivElement, PhoneViewInputProps>((props, ref) => {
  const { value, defaultCountry, sx } = props;
  const flagUrl = getFlagUrl(defaultCountry);

  return (
    <StyledViewWrapper
      ref={ref}
      sx={sx}
    >
      {flagUrl && (
        <PuiBox sx={{ mr: 1, display: 'flex', alignItems: 'center' }}>
          <FlagImage
            src={flagUrl}
            alt={defaultCountry}
          />
        </PuiBox>
      )}
      <PhoneText variant='body-sm-regular'>{formatPhoneNumber(value)}</PhoneText>
    </StyledViewWrapper>
  );
});
