import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, ButtonProps, ChipProps, MenuItemProps, OutlinedInputProps, PopperProps } from '@mui/material';
import { Popper } from '@mui/material';
import Chip from '@mui/material/Chip';

import { PuiBox, PuiButton, PuiMenuItem, PuiStyled, PuiTextInput } from '../../../components';
import { Colors } from '../../../themes';
import type { DisplayType } from '../types';

export interface StyledChipProps extends ChipProps {
  displayType: DisplayType;
}

export const RemainingCountChip: StyledComponent<StyledChipProps> = PuiStyled(Chip, {
  shouldForwardProp: prop => prop !== 'displayType',
})<StyledChipProps>(({ theme, displayType }) => ({
  width: '24px',
  height: '24px',
  padding: 0,
  borderRadius: '4px',
  fontSize: '10px',
  fontWeight: '600',
  backgroundColor: displayType === 'light' ? theme.palette.primary.light : theme.palette.grey[50],
  color: displayType === 'light' ? theme.palette.primary.main : theme.palette.grey[600],
  alignSelf: 'start',

  '& span': {
    padding: 0,
  },
}));

export const StyledChip: StyledComponent<StyledChipProps> = PuiStyled(Chip, {
  shouldForwardProp: prop => prop !== 'displayType',
})<StyledChipProps>(({ theme, displayType }) => ({
  borderRadius: '4px',
  fontSize: '10px',
  fontWeight: '400',
  height: '24px',
  padding: '4px 8px',
  backgroundColor: displayType === 'light' ? theme.palette.primary.light : theme.palette.grey[50],
  color: displayType === 'light' ? theme.palette.primary.main : theme.palette.grey[600],
  textAlign: 'start',
  position: 'relative',

  '& span': {
    padding: 0,
  },

  '& .MuiChip-deleteIcon': {
    position: 'absolute',
    right: '4px',
    opacity: '0',
    strokeWidth: '5',
    margin: 0,
  },

  '&.enable-hover:hover': {
    outline: `1px solid ${theme.palette.grey[100]}`,
    justifyContent: 'start',
    '& .MuiChip-label': {
      width: 'calc(100% - 10px)',
      paddingLeft: 0,
    },

    '& .MuiChip-deleteIcon': {
      opacity: '1',
    },

    '&:has(.MuiChip-deleteIcon:hover)': {
      backgroundColor: Colors.red[50],
    },
  },
}));

export const StyledInput: StyledComponent<OutlinedInputProps> = PuiStyled(PuiTextInput)(({ theme }) => ({
  fontSize: '10px',
  padding: '8px 16px',
  backgroundColor: theme.palette.background.default,

  '&:hover .MuiOutlinedInput-notchedOutline': {
    border: `1px solid ${theme.palette.grey[100]}`,
  },

  '& .MuiOutlinedInput-notchedOutline': {
    border: `1px solid ${theme.palette.grey[100]}`,
  },

  '& .MuiOutlinedInput': {
    border: `1px solid ${theme.palette.grey[100]}`,
  },

  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
    border: `1px solid ${theme.palette.grey[100]}`,

    '&:hover': {
      border: `1px solid ${theme.palette.grey[100]}`,
    },
  },

  '& input': {
    padding: 0,
    color: theme.palette.grey[600],
  },
}));

export const ActionButtonsContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  display: 'flex',
  justifyContent: 'end',
  gap: '24px',
});

export const CreateNewButton: StyledComponent<ButtonProps> = PuiStyled(PuiButton)({
  width: '105px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  gap: '8px',
  padding: '0',
  height: '24px',

  '& p': {
    fontSize: '12px',
    fontWeight: 500,
  },

  '&.MuiButton-sizeSmall': {
    padding: 0,
  },
});

export const StyledPopper: StyledComponent<PopperProps> = PuiStyled(Popper)({
  display: 'flex',
  flexWrap: 'wrap',
  maxWidth: '320px',
  padding: '16px',
  gap: '6px',
  backgroundColor: 'white',
  boxShadow: '0px -4px 12px 0px rgba(0, 0, 0, 0.05), -4px 8px 16px -2px rgba(27, 33, 44, 0.12)',
});

export const SearchInputWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  width: '100%',
  padding: '0 0 6px 0',
  lineHeight: 0,
});

export const CreateNewButtonWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  width: '100%',
  padding: 0,
});

export const ActionButtonsWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)({
  width: '100%',
  padding: '6px 0 0 0',
});

export const ActionButton: StyledComponent<ButtonProps> = PuiStyled(PuiButton)({
  height: '32px',
});

export const StyledMenuItem: StyledComponent<MenuItemProps> = PuiStyled(PuiMenuItem)({
  padding: 0,
});

export const ChipMenuItem: StyledComponent<StyledChipProps> = PuiStyled(StyledChip)(({ theme }) => ({
  '&:hover': {
    outline: `1px solid ${theme.palette.grey[100]}`,
    backgroundColor: `${theme.palette.grey[50]}`,
  },
}));
