import type { ThemeOptions } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material';
import type { ReactElement } from 'react';
import { createContext, useCallback, useEffect, useMemo, useState } from 'react';

import { deepMerge, GlobalTheme, GlobalThemeActionType } from '..';

export interface LocalThemeState {
  /** Current theme */
  theme: ThemeOptions;
  /** Only local themes that overwrite global themes */
  localTheme: ThemeOptions;
  /** Use this to add new local styles to the existing ones */
  updateLocalTheme: (theme: Partial<ThemeOptions>) => void;
  /** Use this to OVERWRITE local styles */
  setLocalTheme: (theme: Partial<ThemeOptions>) => void;
}

const initialValue: LocalThemeState = {
  theme: {},
  localTheme: {},
  updateLocalTheme: () => null,
  setLocalTheme: () => null,
};

export const LocalThemeContext = createContext<LocalThemeState>(initialValue);

export const LocalThemeProvider = ({ children }: { children: ReactElement | ReactElement[] }) => {
  const [localTheme, setLocalTheme] = useState<ThemeOptions>({});
  const [globalTheme, setGlobalTheme] = useState<ThemeOptions>({});

  useEffect(() => {
    const actionType = GlobalThemeActionType.UPDATE_GLOBAL_THEME_RESPONSE;

    const handleUpdateGlobalTheme = (e: CustomEvent<ThemeOptions>) => {
      const globalTheme = e.detail;
      setGlobalTheme(globalTheme);
    };

    window.addEventListener(actionType, handleUpdateGlobalTheme as EventListener);

    return () => window.removeEventListener(actionType, handleUpdateGlobalTheme as EventListener);
  }, [setGlobalTheme]);

  useEffect(() => {
    GlobalTheme.getTheme().then(theme => setGlobalTheme(theme));
  }, [setGlobalTheme]);

  const updateLocalTheme = useCallback(
    (newLocalTheme: Partial<ThemeOptions>) => {
      setLocalTheme(localTheme => deepMerge(localTheme, newLocalTheme));
    },
    [setLocalTheme]
  );

  const theme = useMemo(() => deepMerge(globalTheme, localTheme), [globalTheme, localTheme]);

  return (
    <LocalThemeContext.Provider value={{ theme, localTheme, updateLocalTheme, setLocalTheme }}>
      <ThemeProvider theme={createTheme(theme)}>{children}</ThemeProvider>
    </LocalThemeContext.Provider>
  );
};
