import { includesNormalized } from 'piche.npm.core';

import { CHIP_CHAR_LIMIT, GAP_VALUE, HORIZONTAL_PADDING_VALUE } from './constants';

export const getTooltipTitle = (selectedValues: string[]) => {
  return selectedValues.join(', ');
};

export const getDropdownChipLabel = (label: string) => {
  return label?.length > CHIP_CHAR_LIMIT ? label.substring(0, CHIP_CHAR_LIMIT) + '...' : label;
};

export const containsText = (text: string, searchText: string) => {
  return includesNormalized(text, searchText);
};

interface ChipVisibilityResult {
  visibleChips: string[];
  hiddenChips: string[];
  remainingCount: number;
  accumulatedWidth: number;
  isReady: boolean;
}

interface CalculateChipVisibilityParams {
  selectedValues: string[];
  parentRef: HTMLDivElement;
  currentColumnWidth: number;
}

export const calculateChipVisibility = ({
  selectedValues,
  parentRef,
  currentColumnWidth,
}: CalculateChipVisibilityParams): ChipVisibilityResult => {
  let counterWidth = 0;
  const widths = Array.from(parentRef.children)
    .filter(child => {
      const element = child as HTMLElement;
      if (element.classList.contains('counter')) {
        counterWidth = element.getBoundingClientRect().width;
      }

      return !(element.classList.contains('circle-button') || element.classList.contains('counter'));
    })
    .map(child => (child as HTMLElement).getBoundingClientRect().width);
  const totalWidth = widths.reduce((sum, width, index) => sum + width + (index < widths.length - 1 ? GAP_VALUE : 0), 0);

  let chipsToDisplay = selectedValues;
  let chipsThatDidNotFit: string[] = [];
  const adjustedCurrentColumnWidth = currentColumnWidth - HORIZONTAL_PADDING_VALUE * 2;

  let accumulatedWidth = 0;

  if (totalWidth > adjustedCurrentColumnWidth) {
    chipsToDisplay = [];

    for (let i = 0; i < widths.length; i++) {
      const nextAccumulatedWidth = accumulatedWidth + widths[i] + (i < widths.length - 1 ? GAP_VALUE : 0);
      if (nextAccumulatedWidth + counterWidth > adjustedCurrentColumnWidth) {
        chipsThatDidNotFit = selectedValues.slice(i);
        break;
      }
      accumulatedWidth = nextAccumulatedWidth;
      chipsToDisplay.push(selectedValues[i]);
    }
  }

  const remainingCount = selectedValues.length - chipsToDisplay.length;

  return {
    visibleChips: selectedValues.length !== 0 ? chipsToDisplay : [],
    hiddenChips: chipsThatDidNotFit,
    remainingCount,
    accumulatedWidth,
    isReady: !!counterWidth,
  };
};
