import type { AvatarProps } from '@mui/material';
import { Avatar } from '@mui/material';
import { useEffect, useState } from 'react';

import type { PuiContact } from '../../..';
import { getInitials, hashStringToNumber, PuiGradients, PuiSkeleton } from '../../..';

export interface PuiAvatarProps {
  contact: PuiContact;
  sx?: AvatarProps['sx'];
  size?: number;
  avatarSrc?: string;
  className?: string;
  status?: { type: string; message: string };
}

export const PuiAvatar: React.FC<PuiAvatarProps> = ({ size = 32, sx = {}, contact, avatarSrc, ...props }) => {
  const avatar = avatarSrc || contact.avatarUrl;
  const background = PuiGradients[hashStringToNumber(contact.id, PuiGradients.length)];
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (avatar) {
      const img = new Image();
      setIsLoading(true);

      img.onload = () => {
        setIsLoading(false);
      };

      img.onerror = () => {
        setIsLoading(false);
      };

      img.src = avatar;

      return () => {
        img.onload = null;
        img.onerror = null;
      };
    }
  }, [avatar]);

  if (isLoading) {
    return (
      <Avatar
        className={props.className}
        sx={{
          width: size,
          height: size,
          backgroundColor: 'white',
        }}
      >
        <PuiSkeleton
          variant='circular'
          width={size}
          height={size}
        />
      </Avatar>
    );
  }

  return (
    <Avatar
      alt={contact.name}
      src={avatar ?? undefined}
      className={props.className}
      sx={{
        ...(!avatar && { background }),
        width: size,
        height: size,
        fontSize: '12px',
        ...sx,
      }}
    >
      {!avatar && getInitials(contact.name)}
    </Avatar>
  );
};
