import clsx from 'clsx';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import type { FC } from 'react';

import { PuiIcon, PuiSvgIcon, PuiTypography } from '../../..';
import { StyledButtonContainer, StyledPlayControls, StyledTimeContainer } from './StyledComponents';

dayjs.extend(utc);

interface PlayControlsProps {
  handleClick: () => void;
  currentTime: number;
  duration: number;
  isPlaying: boolean;
  isFullscreen: boolean;
}

const formatTime = (seconds: number): string => dayjs.utc(seconds * 1000).format(seconds >= 3600 ? 'H:mm:ss' : 'mm:ss');

export const PlayControls: FC<PlayControlsProps> = ({
  handleClick,
  currentTime,
  duration,
  isPlaying,
  isFullscreen,
}) => {
  return (
    <StyledPlayControls className={clsx({ fullscreen: isFullscreen })}>
      <StyledButtonContainer
        onClick={handleClick}
        className={clsx({ fullscreen: isFullscreen })}
      >
        {isPlaying ? (
          <PuiSvgIcon
            width={isFullscreen ? 24 : 21}
            height={isFullscreen ? 24 : 21}
            icon={PuiIcon.Pause}
            stroke='white'
          />
        ) : (
          <PuiSvgIcon
            width={isFullscreen ? 20 : 18}
            height={isFullscreen ? 20 : 18}
            icon={PuiIcon.PlayFilled}
            stroke='white'
          />
        )}
      </StyledButtonContainer>
      <PuiTypography
        variant='body-m-medium'
        color='white'
        minWidth={100}
      >
        <StyledTimeContainer>{formatTime(currentTime)}</StyledTimeContainer>/
        <StyledTimeContainer className='left'>{formatTime(duration)}</StyledTimeContainer>
      </PuiTypography>
    </StyledPlayControls>
  );
};
