import type { ThemeOptions } from '@mui/material';

export enum GlobalThemeActionType {
  GET_GLOBAL_THEME = 'PUI_GET_GLOBAL_THEME',
  UPDATE_GLOBAL_THEME = 'PUI_UPDATE_GLOBAL_THEME',
  GET_GLOBAL_THEME_RESPONSE = 'PUI_GET_GLOBAL_THEME_RESPONSE',
  UPDATE_GLOBAL_THEME_RESPONSE = 'PUI_UPDATE_GLOBAL_THEME_RESPONSE',
}

export class GlobalTheme {
  public static dispatchEvent = (event: GlobalThemeActionType, detail?: unknown) => {
    const customEvent = new CustomEvent(event, { detail, bubbles: true, composed: true });
    dispatchEvent(customEvent);
  };

  public static getTheme = () =>
    new Promise<ThemeOptions>(resolve => {
      const eventType = GlobalThemeActionType.GET_GLOBAL_THEME_RESPONSE;

      const handleReturnGlobalTheme = (e: CustomEvent<ThemeOptions>) => {
        window.removeEventListener(eventType, handleReturnGlobalTheme as EventListener);
        resolve(e.detail);
      };

      window.addEventListener(eventType, handleReturnGlobalTheme as EventListener);

      this.dispatchEvent(GlobalThemeActionType.GET_GLOBAL_THEME);
    });

  public static updateTheme = (newTheme: Partial<ThemeOptions>) => {
    this.dispatchEvent(GlobalThemeActionType.UPDATE_GLOBAL_THEME, newTheme);
  };
}
