import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, InputProps } from '@mui/material';

import { PuiBox, PuiStyled, PuiTextInput } from '../../../';

export const TextInputContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  height: '100%',
  width: '100%',
}));

export const TextInput: StyledComponent<InputProps> = PuiStyled(PuiTextInput)({
  padding: '10px 17px',
  lineHeight: '19.2px',
  fontSize: '12px',
  height: '100%',
  width: '100%',
  '& textarea': {
    height: '100%',
    width: '100%',
    paddingBottom: '1px',
    textAlign: 'right',
  },
  '& input': { textAlign: 'right' },
  '& fieldset': {
    border: 'none',
  },
});

export const PercentageBox: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ theme }) => ({
  marginLeft: '3px',
  color: theme.palette.grey[400],
}));
