import type { StyledComponent } from '@emotion/styled';
import type { BoxProps } from '@mui/material';

import { PuiBox, PuiStyled } from '../../../';

export const StyledViewBooleanContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  width: '42px',
  height: '42px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}));

export const StyledViewSwitchContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  width: '42px',
  height: '36px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}));

export const StyledViewBooleanEmpty: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ color }) => ({
  border: '1px solid ' + color,
  borderRadius: '4px',
  height: '24px',
  width: '24px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}));

export const StyledViewCheckmarkEmpty: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ color }) => ({
  border: '2px solid ' + color,
  borderRadius: '4px',
  height: '20px',
  width: '20px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}));

export const StyledViewCheckedIconContainer: StyledComponent<BoxProps & { color: string }> = PuiStyled(PuiBox)<{
  color: string;
}>(({ color }) => ({
  width: '24px',
  height: '24px',
  borderRadius: '4px',
  backgroundColor: color,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}));

export const StyledSwitchBackground: StyledComponent<BoxProps & { color: string; checked: boolean }> = PuiStyled(
  PuiBox
)<{ color: string; checked: boolean }>(({ color, checked, theme }) => ({
  borderRadius: '10px',
  display: 'flex',
  justifyContent: checked ? 'end' : 'start',
  alignItems: 'center',
  height: '18px',
  width: '28px',
  margin: '0 0',
  backgroundColor: checked ? color : theme.palette.grey[50],
}));

export const StyledSwitchContent: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ theme }) => ({
  borderRadius: '14px',
  backgroundColor: theme.palette.common.white,
  height: '14px',
  width: '14px',
  margin: '0 2px',
}));
