import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, TypographyProps } from '@mui/material';

import { PuiBox, PuiStyled, PuiTypography } from '../../../';

export const TextField: StyledComponent<TypographyProps> = PuiStyled(PuiTypography)({
  display: 'flex',
  justifyContent: 'flex-end',
  padding: '0 16px',
  '&:focus-visible': { outline: 'none' },
  overflow: 'hidden',
  textOverflow: 'ellipsis',
});

export const TextInputContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  height: '100%',
  width: '100%',
}));

export const TextWrapper: StyledComponent<React.HTMLAttributes<HTMLSpanElement>> = PuiStyled('span')({
  display: 'inline-block',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
  textAlign: 'right',
});

export const PercentageText: StyledComponent<TypographyProps> = PuiStyled(PuiTypography)({
  textAlign: 'right',
  marginLeft: '3px',
});
