import Modal from '@mui/material/Modal';
import type { FC, ReactNode } from 'react';

import { PuiIcon, PuiSvgIcon } from '../';
import { PuiBox, PuiIconButton, PuiStack, PuiStyled, PuiTypography } from './';

export interface PuiModalProps {
  className?: string;
  title?: string;
  subtitle?: ReactNode;
  children: ReactNode;
  headerButtons?: ReactNode;
  open: boolean;
  onClose: () => void;
  width?: string;
  size?: 'medium' | 'large' | 'xlarge';
  labelledby?: string;
  describedby?: string;
  titleIcon?: ReactNode;
}

const sizes = {
  medium: '500px',
  large: '668px',
  xlarge: '998px',
};

const ModalBody = PuiStyled(PuiBox)(({ theme }) => ({
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  background: '#fff',
  display: 'flex',
  flexDirection: 'column',
  gap: '24px',
  maxWidth: 'calc(100% - 70px)',
  borderRadius: '24px',
  padding: '32px 24px',
  outline: 'none',
  boxSizing: 'border-box',
  '& .MuiTypography-body-m-medium': {
    color: theme.palette.grey[400],
  },
}));

const CloseButton = PuiStyled(PuiIconButton)({
  padding: 0,
  '&:hover': {
    background: 'none',
  },
});

const ButtonsWrapper = PuiStyled(PuiStack)({
  position: 'absolute',
  top: '32px',
  right: '24px',
  zIndex: 1,
});

const ModalTitle = PuiStyled(PuiTypography)(({ theme }) => ({
  wordBreak: 'break-word',
  [theme.breakpoints.down('sm')]: {
    fontSize: '18px',
  },
}));

export const PuiModal: FC<PuiModalProps> = ({
  className,
  title,
  subtitle,
  children,
  headerButtons,
  open,
  onClose,
  width,
  size = 'medium',
  labelledby,
  describedby,
  titleIcon,
}) => {
  const modalBodyWidth = width ?? sizes[size];

  return (
    <Modal
      open={open}
      onClose={onClose}
      aria-labelledby={labelledby ?? ''}
      aria-describedby={describedby ?? ''}
      className={className ?? ''}
    >
      <ModalBody width={modalBodyWidth}>
        <ButtonsWrapper
          direction='row'
          alignItems='center'
          gap='4px'
        >
          {headerButtons}
          <CloseButton onClick={onClose}>
            <PuiSvgIcon
              icon={PuiIcon.XClose}
              width={24}
              height={24}
            />
          </CloseButton>
        </ButtonsWrapper>
        {titleIcon}
        {(title || subtitle) && (
          <PuiStack gap='16px'>
            {title && <ModalTitle variant='h5'>{title}</ModalTitle>}
            {subtitle}
          </PuiStack>
        )}
        {children}
      </ModalBody>
    </Modal>
  );
};

export { Modal as PuiModalBase };
