type BaseSvgIconProps = Omit<SvgIconProps, 'children'>;

interface IconProps extends BaseSvgIconProps {
  icon: PuiIcon | DuoIcon;
  children?: never;
}

interface ChildrenProps extends BaseSvgIconProps {
  icon?: never;
  children: React.ReactNode;
}

export type PuiSvgIconProps = IconProps | ChildrenProps;

export const PuiSvgIcon = (props: PuiSvgIconProps) => {
  const { icon, width, height, stroke, ...other } = props;

  if (icon) {
    return (
      <SvgIcon
        stroke={stroke ?? 'currentColor'}
        strokeWidth={2}
        sx={{ width: width ?? 24, height: height ?? 24, color: stroke ?? 'currentColor' }}
        {...other}
      >
        {icons[icon]}
      </SvgIcon>
    );
  }

  return <SvgIcon {...props} />;
};
