import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, InputProps } from '@mui/material';

import { Colors, PuiBox, PuiStyled, PuiTextInput } from '../../../';

export const TextInputContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  alignItems: 'center',
  height: '100%',
  width: '100%',
}));

export const CustomTextInput: StyledComponent<InputProps> = PuiStyled(PuiTextInput)(({ theme }) => ({
  padding: '0 16px',
  fontSize: '12px',
  width: '100%',
  height: 'fit-content',
  minHeight: '100%',
  backgroundColor: theme.palette.background.paper,
  '& fieldset': {
    border: 'none',
  },
  '& .MuiInputBase-input': {
    height: '100%',
    width: '100%',
    padding: 0,
    lineHeight: '1.5',
  },
  '&.wrapper': {
    backgroundColor: theme.palette.background.default,
    border: '1px solid',
    borderColor: Colors.blue.base,
    minHeight: '42px',
    position: 'absolute',
    left: '-1px',
    top: '-1px',
    width: 'calc(100% + 2px)',
    zIndex: 1,
  },
  '&.overflowed': {
    padding: '0 0 0 16px',
  },
}));
