import { type FC, forwardRef, type RefObject } from 'react';

import { formatNumber } from '../../utils';
import { TextInputContainer } from '../edit/StyledComponents';
import { NumberFormatEnum } from '../types';
import { PercentageText, TextField, TextWrapper } from './StyledComponents';

interface PuiNumberViewProps {
  value: number | undefined;
  decimalPlaces?: number;
  format?: NumberFormatEnum;
  percentage?: boolean;
}

type RefType = ((instance: HTMLDivElement | null) => void) | RefObject<HTMLDivElement> | null | undefined;

export const PuiNumberView: FC<PuiNumberViewProps> = forwardRef((props, ref: RefType) => {
  const { value, decimalPlaces = 0, format = NumberFormatEnum.SPACE_PERIOD, percentage = false } = props;

  return (
    <TextInputContainer>
      <TextField
        variant='body-sm-regular'
        ref={ref}
      >
        <TextWrapper>{formatNumber(value, format, decimalPlaces)}</TextWrapper>
        {percentage && value && <PercentageText variant='body-sm-regular'>%</PercentageText>}
      </TextField>
    </TextInputContainer>
  );
});
