import type { BoxProps } from '@mui/material';

import { PuiBox, PuiStyled, PuiSwitch } from '../../../components';
import { useTheme } from '../../../hooks';
import { PuiIcon, PuiSvgIcon } from '../../../icons';
import { BooleanDisplayConfigType } from '../types';

export const IconByDisplayType: Record<BooleanDisplayConfigType, PuiIcon> = {
  [BooleanDisplayConfigType.Heart]: PuiIcon.HeartFilled,
  [BooleanDisplayConfigType.Star]: PuiIcon.StarFilled,
  [BooleanDisplayConfigType.ThumbsUp]: PuiIcon.ThumbsUp,
  [BooleanDisplayConfigType.Flag]: PuiIcon.Flag2,
  [BooleanDisplayConfigType.Checkmark]: PuiIcon.Check,
  [BooleanDisplayConfigType.Switch]: PuiIcon.CheckboxChecked,
};

const StyledPuiBox = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  padding: '4px',
  width: '24px',
  height: '24px',
  borderRadius: '4px',
}));

interface BooleanIconProps {
  color?: string;
  displayType: BooleanDisplayConfigType;
  isChecked: boolean;
  sx: BoxProps['sx'];
}

export const BooleanIcon = ({ displayType, color, isChecked, sx = {} }: BooleanIconProps) => {
  const theme = useTheme();

  if (displayType === BooleanDisplayConfigType.Switch) {
    return <PuiSwitch checked={isChecked} />;
  }

  return (
    <StyledPuiBox
      sx={
        isChecked
          ? { backgroundColor: color ?? theme.palette.primary.main, ...sx }
          : { border: `1px solid ${theme.palette.grey[100]}`, ...sx }
      }
    >
      <PuiSvgIcon
        icon={IconByDisplayType[displayType]}
        width={16}
        height={16}
        stroke={isChecked ? theme.palette.common.white : theme.palette.grey[200]}
      />
    </StyledPuiBox>
  );
};
