import { colors } from '@mui/material';

import { ColorShade } from './ColorShade';

export type MuiColorKey = keyof typeof colors;

interface PuiColorGeneratorConfig {
  allowedColors: MuiColorKey[];
  allowedShades: ColorShade[];
  defaultColor?: string;
}

export class PuiColorGenerator {
  private static allowedColors: MuiColorKey[] = [
    'blue',
    'purple',
    'deepPurple',
    'indigo',
    'teal',
    'green',
    'deepOrange',
    'brown',
    'grey',
  ];
  private static allowedShades: ColorShade[] = [ColorShade.S600, ColorShade.S500, ColorShade.S400];
  private static defaultColor: string = colors.blue[500];

  public static configure(config: Partial<PuiColorGeneratorConfig>): void {
    if (config.allowedColors) this.allowedColors = config.allowedColors;
    if (config.allowedShades) this.allowedShades = config.allowedShades;
    if (config.defaultColor) this.defaultColor = config.defaultColor;
  }

  public static getRandomColor(): string {
    try {
      const randomColorKey = this.allowedColors[Math.floor(Math.random() * this.allowedColors.length)];
      const randomShade = this.allowedShades[Math.floor(Math.random() * this.allowedShades.length)];

      const colorObject = colors[randomColorKey];
      if (randomShade in colorObject) {
        return colorObject[randomShade as keyof typeof colorObject];
      }
    } catch (error) {
      console.error('Error generating random color, falling back to default', error);
    }

    return this.defaultColor;
  }

  public static getAllowedColors(): MuiColorKey[] {
    return [...this.allowedColors];
  }

  public static getAllowedShades(): ColorShade[] {
    return [...this.allowedShades];
  }
}
