import type { StyledComponent } from '@emotion/styled';
import type { BoxProps, MenuItemProps, MenuListProps, SelectProps } from '@mui/material';

import type { PuiSvgIconProps } from '../../../';
import { PuiBox, PuiMenuItem, PuiMenuList, PuiSelect, PuiStyled, PuiSvgIcon } from '../../../';
import { commonSelectStyles } from '../../StyledComponents';

interface StatusDropdownProps {
  textcolor: string;
  background: string;
}

export const StatusSelect: StyledComponent<SelectProps & StatusDropdownProps> = PuiStyled(
  PuiSelect
)<StatusDropdownProps>(({ textcolor, background, theme }) => ({
  ...commonSelectStyles(textcolor, background, theme),
  '&.StatusSelect-empty': {
    '.MuiSvgIcon-root': {
      display: 'inline-block',
    },
  },
  '&.cell-wrapped': {
    border: '1px solid' + theme.palette.common.white,
    fontWeight: 500,
    '& .round-indicator': {
      marginRight: '0',
    },
  },
  height: '100%',
}));

export const StatusSelectMenuList: StyledComponent<MenuListProps> = PuiStyled(PuiMenuList)({
  '&.MuiList-padding': {
    padding: '4px',
  },
});

export const StatusDropdownItem: StyledComponent<MenuItemProps> = PuiStyled(PuiMenuItem)(() => ({
  borderRadius: '4px',
  fontSize: '12px',
}));

export const StatusDropdownViewItem: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  borderRadius: '4px',
  fontSize: '12px',
  position: 'relative',
}));

export const StyledFillStatusItem: StyledComponent<MenuItemProps & { color: string }> = PuiStyled(StatusDropdownItem)<{
  color: string;
}>(({ theme, color }) => ({
  color: theme.palette.grey[50],
  display: 'flex',
  justifyContent: 'center',
  backgroundColor: color,
  paddingTop: '0px',
  paddingBottom: '0px',
  '&:not(:last-child)': {
    marginBottom: '4px',
  },
  '&:hover, &.Mui-selected:hover': {
    opacity: 0.8,
    backgroundColor: color,
  },
  '&.Mui-selected, &.Mui-selected.Mui-focusVisible, &.MuiMenuItem-root.Mui-focusVisible': {
    backgroundColor: color,
    opacity: 0.9,
  },
}));

export const StyledFillStatusItemContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ theme }) => ({
  padding: '10px 16px',
  color: theme.palette.grey[50],
  height: '100%',
  display: 'flex',
  alignItems: 'center',
}));

export const StyledLeftIndicatorStatusItem: StyledComponent<MenuItemProps> = PuiStyled(StatusDropdownItem)(
  ({ theme }) => ({
    display: 'flex',
    backgroundColor: theme.palette.grey['A100'],
    color: theme.palette.grey[400],
    borderRadius: '4px',
    border: '1px solid ' + theme.palette.grey[50],
    padding: '0px',
    '&:not(:last-child)': {
      marginBottom: '4px',
    },
    '&:hover, &.Mui-selected:hover': {
      opacity: 0.8,
      backgroundColor: theme.palette.grey['A100'],
      color: theme.palette.grey[400],
    },
    '&.Mui-selected, &.Mui-selected.Mui-focusVisible, &.MuiMenuItem-root.Mui-focusVisible': {
      opacity: 0.9,
      backgroundColor: theme.palette.grey['A100'],
      color: theme.palette.grey[400],
    },
  })
);

export const LeftIndicator: StyledComponent<BoxProps & { color: string }> = PuiStyled(PuiBox)<{ color: string }>(
  ({ color }) => ({
    position: 'absolute',
    left: 6,
    width: '6px',
    height: '26px',
    backgroundColor: color,
    borderRadius: '4px',
    marginRight: '16px',
  })
);

export const StyledClassicStatusItem: StyledComponent<MenuItemProps> = PuiStyled(StatusDropdownItem)(({ theme }) => ({
  backgroundColor: theme.palette.common.white,
  color: theme.palette.grey[400],
  padding: 0,
  paddingRight: '6px',
  borderRadius: 0,
  '&:not(:last-child)': {
    marginBottom: '1px',
  },
  '&:hover, &.Mui-selected:hover': {
    opacity: 0.8,
    backgroundColor: theme.palette.grey[50],
    color: theme.palette.grey[400],
  },
  '&.Mui-selected, &.Mui-selected.Mui-focusVisible, &.MuiMenuItem-root.Mui-focusVisible': {
    opacity: 0.9,
    backgroundColor: theme.palette.common.white,
    color: theme.palette.grey[400],
  },
}));

export const RoundIndicator: StyledComponent<BoxProps & { color: string }> = PuiStyled(PuiBox)<{ color: string }>(
  ({ color }) => ({
    position: 'absolute',
    width: '12px',
    height: '12px',
    backgroundColor: color,
    borderRadius: '50%',
    flexShrink: 0,
    left: 6,
    marginRight: '1em',
    '&.view': {
      marginRight: 0,
    },
  })
);

export const StyledRoundStatusItem: StyledComponent<MenuItemProps> = PuiStyled(StatusDropdownItem)(({ theme }) => ({
  backgroundColor: theme.palette.common.white,
  color: theme.palette.grey[400],
  borderRadius: '4px',
  padding: 0,
  '&:not(:last-child)': {
    marginBottom: '2px',
  },
  '&:hover, &.Mui-selected:hover': {
    opacity: 0.8,
    color: theme.palette.grey[400],
  },
  '&.Mui-selected, &.Mui-selected.Mui-focusVisible, &.MuiMenuItem-root.Mui-focusVisible': {
    opacity: 0.9,
    color: theme.palette.grey[400],
  },
}));

export const ClassicStatusBubble: StyledComponent<BoxProps & { color: string }> = PuiStyled(PuiBox)<{ color: string }>(
  ({ color }) => ({
    position: 'relative',
    display: 'flex',
    padding: '4px 16px',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: '72px',
    color,
    overflow: 'hidden',
    '&:after': {
      position: 'absolute',
      content: '""',
      width: '100%',
      height: '100%',
      backgroundColor: color,
      filter: 'opacity(0.2)',
      top: 0,
      left: 0,
    },
  })
);

export const LeftIndicatorOptionContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  alignItems: 'center',
  fontSize: '12px',
  fontWeight: 500,
  padding: '9px 16px',
  paddingLeft: '2px',
  height: '100%',
}));

export const RoundOptionContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  width: '100%',
  padding: '10px 16px 10px 0',
  height: '100%',
  gap: '5px',
  '& > :not(:last-child)': {
    marginLeft: '28px',
  },
}));

export const ClassicStatusItemContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  display: 'flex',
  padding: '6px 4px',
  alignItems: 'center',
  border: '1px solid transparent',
  justifyContent: 'space-between',
  height: '100%',
}));

export const StyledLeftIndicatorContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ theme }) => ({
  marginLeft: '28px',
  padding: 0,
  textOverflow: 'ellipsis',
  overflow: 'hidden',
  whiteSpace: 'nowrap',
  color: theme.palette.grey[500],
}));

export const StyledOptionTypography: StyledComponent<BoxProps & { color?: string }> = PuiStyled(PuiBox)<{
  color?: string;
}>(({ theme, color }) => ({
  fontSize: '12px',
  fontWeight: 500,
  textOverflow: 'ellipsis',
  overflow: 'hidden',
  whiteSpace: 'nowrap',
  color: color ?? theme.palette.grey[500],
  '&.fill': {
    color: theme.palette.common.white,
  },
}));

export const StyledCloseIcon: StyledComponent<PuiSvgIconProps> = PuiStyled(PuiSvgIcon)(() => ({
  marginRight: '38px',
}));

export const StyledOpenIcon: StyledComponent<PuiSvgIconProps> = PuiStyled(PuiSvgIcon)(() => ({
  fontSize: '8px',
  cursor: 'pointer',
  position: 'absolute',
  right: '16px',
}));
