import type { StyledComponent } from '@emotion/styled';
import type { BoxProps } from '@mui/material';

import { PuiBox, PuiStyled } from '../../../';
import { StatusDropdownViewItem } from '../edit/StyledComponents';

export const StyledViewLeftIndicatorStatusItem: StyledComponent<BoxProps> = PuiStyled(StatusDropdownViewItem)(
  ({ theme }) => ({
    backgroundColor: theme.palette.common.white,
    height: '100%',
    width: '100%',
    color: theme.palette.grey[400],
    alignItems: 'center',
    display: 'inline-flex',
    '&:hover, &.Mui-selected:hover': {
      color: theme.palette.grey[400],
    },
  })
);

export const StyledViewClassicStatusItem: StyledComponent<BoxProps> = PuiStyled(StatusDropdownViewItem)(
  ({ theme }) => ({
    backgroundColor: theme.palette.common.white,
    color: theme.palette.grey[400],
    height: '100%',
    width: '100%',
    alignItems: 'center',
    display: 'inline-flex',
    '&:hover, &.Mui-selected:hover': {
      color: theme.palette.grey[400],
    },
  })
);

export const StyledViewFillStatusItem: StyledComponent<BoxProps & { color: string }> = PuiStyled(
  StatusDropdownViewItem
)<{
  color: string;
}>(({ theme, color }) => ({
  height: '100%',
  color: theme.palette.grey[50],
  width: '100%',
  alignItems: 'center',
  display: 'inline-flex',
  backgroundColor: color,
  '&:hover, &.Mui-selected:hover': {
    backgroundColor: color,
  },
}));

export const StyledViewRoundStatusItem: StyledComponent<BoxProps> = PuiStyled(StatusDropdownViewItem)(({ theme }) => ({
  backgroundColor: theme.palette.common.white,
  color: theme.palette.grey[400],
  width: '100%',
  height: '100%',
  alignItems: 'center',
  display: 'inline-flex',
  '&:hover, &.Mui-selected:hover': {
    opacity: 0.8,
    color: theme.palette.grey[400],
  },
}));

export const StyledStatusView: StyledComponent<BoxProps> = PuiStyled(PuiBox)(({ theme }) => ({
  minWidth: 0,
  width: '100%',
  height: '100%',
  fontFamily: theme.typography.fontFamily,
  '&:hover .hover-status-actions': { display: 'flex' },
}));

export const StatusValueWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  minWidth: 0,
  width: '100%',
  height: '100%',
}));

export const HoverIconsWrapper: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  gap: '7px',
  height: '100%',
  display: 'flex',
  alignItems: 'center',
}));

export const HoverIconsWrapperContainer: StyledComponent<BoxProps> = PuiStyled(PuiBox)(() => ({
  height: '100%',
  display: 'none',
  alignItems: 'center',
  padding: '0 16px 0 2px',
  justifyContent: 'flex-end',
}));
