import type { BoxProps } from '@mui/material';
import { forwardRef } from 'react';

import { PuiBox, PuiTooltip } from '../../../components';
import { useTheme } from '../../../hooks';
import { getTooltipTitle } from '../utils';
import { StyledTagCell } from './StyledComponents';

interface BaseTagCellProps {
  selectedValues: { id: string; title: string }[];
  disabled?: boolean;
  tagCellSx?: BoxProps['sx'];
  children: React.ReactNode;
  isTooltipOpen: boolean;
  onTooltipClose: () => void;
  onTooltipOpen: () => void;
  onClick?: () => void;
  className?: string;
}

export const BaseTagCell = forwardRef<HTMLElement, BaseTagCellProps>(
  (
    { selectedValues, disabled, tagCellSx, children, isTooltipOpen, onTooltipClose, onTooltipOpen, onClick, className },
    ref
  ) => {
    const theme = useTheme();

    return (
      <PuiTooltip
        title={getTooltipTitle(selectedValues.map(({ title }) => title))}
        placement='top'
        arrow
        open={isTooltipOpen}
        onClose={onTooltipClose}
        disableFocusListener
        disableTouchListener
        onMouseEnter={onTooltipOpen}
        onMouseLeave={onTooltipClose}
        componentsProps={{
          tooltip: {
            sx: {
              '&.MuiTooltip-tooltip': {
                background: `${theme.palette.background.default}`,
                color: `${theme.palette.grey[500]}`,
                boxShadow: '0px -4px 12px 0px rgba(0, 0, 0, 0.05), -4px 8px 16px -2px rgba(27, 33, 44, 0.12);',
                fontSize: '10px',
                maxWidth: 'none',
              },
              '& .MuiTooltip-arrow::before': {
                background: `${theme.palette.background.default}`,
              },
            },
          },
        }}
      >
        <PuiBox>
          <StyledTagCell
            ref={ref}
            onClick={onClick}
            className={className}
            sx={{
              cursor: disabled ? 'not-allowed' : 'pointer',
              ...tagCellSx,
            }}
          >
            {children}
          </StyledTagCell>
        </PuiBox>
      </PuiTooltip>
    );
  }
);
